

*{margin: 0; padding: 0;font-family: Inter;}
.view-datum__populated{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(255, 255, 255);
    padding: 30px 0;
    margin-bottom: 18px;
    width: 50%;
    box-shadow: 2px 2px 10px rgb(223, 223, 223);
    

}

.imageHoler{
    display: flex;
    width: 63px;
    height: 63px;
    margin: 0 22px;
    border-radius: 50%;
    
}

.imageHoler img{
    
    width: 63px;
    height: 63px;
    border-radius: 50%;
    
}

.content-holder{

    display: flex;
    width: 90%;
    height: 63px;

}

.name-job{

    display: flex;
    flex-direction: column;
    gap: 8px;
    align-items: start;
    justify-content: center;
    padding: 12px;
}

.name-job h3{
    
    font-size: 1.2em;
    font-weight: 600;
}


.name-job h4{
   
    font-size: 1em;
    font-weight: 400;
    
}


.eddit-delete{
    display: flex;
    gap: 30px;
    padding: 12px;
    margin: 0 10px;
    cursor: pointer;
}



.editButton{
    width: 22px;
    height: 22px;
    flex-shrink: 0;
    cursor: pointer;
}


.deleteButton{
    width: 23px;
    height: 25px;
    flex-shrink: 0;
    cursor: pointer;
}




