*{margin: 0; padding: 0;}

.addData{
    display: flex;
    flex-direction: column;
}

.addData-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.nav{
     display: flex;
}

.backArrow{
    padding: 12px;
}

.imageFile{
    display: flex;
    width: 163px;
    height: 163px;
    border: 2px transparent solid; 
}

.imageFile__holder{

    position: relative;
    width: 161px;
    height: 161px;
    border-radius: 50%;
    border: 1px #164B60 solid;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: transparent;
}

.plus-icon{

    height: 36px;
    width: 36px;
    position: relative;
    top: 70%;
    right: 22%;
}

.add-icon{

    height: 35px;
    width: 35px;
    cursor: pointer;

}

.image-default-wrapper{ 
    /* position: relative;*/
    width: 161px;
    height: 161px;
    display: flex;
    justify-content: center;
    align-items: center;
}



.image-selected{
    position: relative;
    width: 161px;
    height: 161px;
    border-radius: 50%;
}

.form-container{
    width: 100%;
    padding: 50px 0;
}

form{

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 24px;
    padding: 8px 0;
}
form input{
    height: auto;
    width: 50%;
    padding: 22px;
    border: none;
    box-shadow: 2px 2px 10px rgb(223, 223, 223);

}

.addData-btn{
    padding: 18px 30px;
    text-transform: capitalize;
    background-color: #164B60; 
    color: #ffffff;
    font-family: Inter;
    font-size: 1.1em;
    font-style: normal;
    font-weight: 600;
    border: none;
    cursor: pointer;
}

