
*{margin: 0;padding: 0; font-family: Inter;}
.home{
    
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.add-button-wrapper{
    display: flex;
    align-items: end;
    justify-content: end;
    width: 50%;
    padding: 30px 0;
}
.add-member{
   
    justify-self: end;
    padding: 10px 26px;
    text-transform: capitalize;
    background-color: #164B60;  
}

.add-member Button{

    color: #ffffff;
    font-family: monospace;
    font-size: 1em;
    font-style: normal;
    font-weight: 600;
   
}

.view-datum{
    display: flex;
    flex-direction: column;
   align-items: center;
   justify-content: center;
    width: 100%;
    background-color: aquamarine;
    padding: 5px;
}

.no-member{

    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(255, 255, 255);
    padding: 30px 0;
    width: 50%;
    box-shadow: 2px 2px 10px rgb(223, 223, 223);
    color: black;
    font-size: 2em;
    font-weight: 400;

}